<template>
  <div class="location-phone">
    <label>
      <input type="checkbox" v-model="dataRowItemOption.telephone" @change="setDataRowItemOption" value="true" />일반
      전화
    </label>
    <label>
      <input
        type="checkbox"
        v-model="dataRowItemOption.mobilephone"
        @change="setDataRowItemOption"
        value="true"
      />휴대폰
    </label>
  </div>
</template>
<script>
import { defineComponent, ref, computed } from 'vue';
import { useStore } from 'vuex';

export default defineComponent({
  props: {
    index: Number,
    dataRow: Object,
  },
  setup(props) {
    const store = useStore();
    // default value
    let dataRowItemOption = props.dataRow
      ? computed(() => props.dataRow.item.itemOption)
      : ref({
          telephone: false,
          mobilephone: false,
        });

    store.commit('setDataRowItemOption', {
      index: props.index,
      dataRowItemOption: dataRowItemOption.value,
    });

    // 선택한 data type을 store에 저장
    const setDataRowItemOption = () => {
      store.commit('setDataRowItemOption', {
        index: props.index,
        dataRowItemOption: dataRowItemOption.value,
      });
      console.log('dataRowItemOption -->', store.getters.getDataRowItemOption(props.index));
    };

    return {
      dataRowItemOption,
      setDataRowItemOption,
    };
  },
});
</script>

<style lang="scss" scoped>
.data-row-options {
  appearance: auto !important;
}

